import { d as db_connect } from "../../../../chunks/database.js";
import { ObjectId } from "mongodb";
import { e as error } from "../../../../chunks/index2.js";
import { s as serializeMongoData } from "../../../../chunks/wrappers.js";
const load = async ({ params }) => {
  const db_client = await db_connect("vico_db");
  const collection = db_client.collection("users");
  const users = await collection.find().toArray();
  let results = await serializeMongoData(users);
  if (results) {
    return results;
  }
  throw error(404, "Not found");
};
const actions = {
  addUser: async ({ request }) => {
    const db_client = await db_connect("vico_db");
    const collection = db_client.collection("users");
    const values = await request.formData();
    const username = values.get("username");
    const firstname = values.get("firstname");
    const lastname = values.get("lastname");
    const email = values.get("email");
    const status = values.get("status");
    const role = values.get("role");
    const user = {
      "username": username,
      "firstname": firstname,
      "lastname": lastname,
      "email": email,
      "status": status,
      "role": role
    };
    await collection.insertOne(user);
    return { success: true };
  },
  editUser: async ({ request }) => {
    const db_client = await db_connect("vico_db");
    const collection = db_client.collection("users");
    const values = await request.formData();
    const _id = values.get("_id");
    const username = values.get("username");
    const firstname = values.get("firstname");
    const lastname = values.get("lastname");
    const email = values.get("email");
    const status = values.get("status");
    const role = values.get("role");
    const user = {
      "username": username,
      "firstname": firstname,
      "lastname": lastname,
      "email": email,
      "status": status,
      "role": role
    };
    await collection.updateOne(
      { _id: new ObjectId(_id) },
      {
        $set: user
      }
    );
    return { success: true };
  }
};
export {
  actions,
  load
};
